import React, {useEffect} from 'react'
import styled from "styled-components";
import AOS from "aos";
import "aos/dist/aos.css";

export const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  h1 {
    color: #b19cd9;
    font-size: 60px;
    font-family: copperplate;
  }
  @media screen and (max-width: 700px) {
    h1 {
      font-size: 50px;
    }
  }
`;

export const BoxContainer = styled.div`
  width: 90%;
  background-color: #ffe8e5;
  -webkit-box-shadow: -8px 9px 0px 5px #BFE6FF; 
  box-shadow: -8px 9px 0px 5px #BFE6FF;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h2 {
    color: #b19cd9;
    font-size: 30px;
  }
  p {
    color: #CDB7F6;
    font-size: 20px;
  }
  @media screen and (max-width: 700px) {
    width: 85%;
    h2 {
      font-size: 25px;
      width: 90%;
    }
    p {
    font-size: 16px;
    width: 90%;
  }
  }
`;

function FAQ() {
  useEffect(() => {
    AOS.init({
      duration : 2000
    });
  }, []);
  return (
    <Container id="faq">
      <h1>FAQ</h1>
      <BoxContainer data-aos="fade-up">
        <h2>1.How can I mint a Shiba NFT?</h2>

        <p>You can mint directly on our official website when pre-sale and public sale is live by connecting to your Solana wallet<br/>

        Any unsold NFTs will be listed on secondary market afterwards</p>
      </BoxContainer>
      <BoxContainer data-aos="fade-up">
      <h2>2.How much is 1 Shiba NFT?</h2>

      <p>Pre-sale: 0.66 SOL<br/>
      Public sale: 0.88 SOL</p>
      </BoxContainer>
      <BoxContainer data-aos="fade-up">
      <h2>3.What is the total supply?</h2>

      <p>There are 1699 Shiba NFTs in total</p>
      </BoxContainer>
      <BoxContainer data-aos="fade-up">
      <h2>4.Who can join the pre-sale?</h2>

      <p>For pre-sale 1: Our OG members and community contributors<br/>
      For pre-sale 2: Our whitelisted early supporters<br/>
      Public sale: All Shiba NFTs lovers</p>
      </BoxContainer>
      <BoxContainer data-aos="fade-up">
      <h2>5.How can I become an OG member?</h2>

      <p>The first 50 discord members who completed a simple task can become an OG member.<br/>
      Early community contributors will be invited to become an OG member</p>
      </BoxContainer>
    </Container>
  )
}

export default FAQ