import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    margin-top: 50px;
    background-color:  #EABFB9;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
        color: #ffebed;
        font-family: copperplate;
        font-size: 50px;
    }
    @media screen and (max-width: 700px) {
      h1 {
        font-size: 40px;
      }
    }
`;

export const MasterContainer = styled.div`
  display: flex;
  width: 100%;
  width: 60%;
`;

export const LineDiv = styled.div`
  position: absolute;
  width: 4px;
  height: 100vh;
  background-color: #A67B5B;
  left: 15%;
  div {
    width: 30px;
    height: 30px;
    background-color: #A67B5B;
    position: absolute;
    border-radius: 50px;
    left: -13px;
    top: 60px;
  }
  @media screen and (max-width: 700px) {
    left: 10%;
    height: 90vh;
    div {
      width: 20px;
      height: 20px;
      left: -8px;
    }
  }
`;

export const BoxDiv = styled.div`
  width: 120%;
  border-radius: 10px;
  background-color: #FFFEEB;
  margin-bottom: 30px;
  display: flex;
  height: 160px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  -webkit-box-shadow: -8px 7px 20px -1px rgba(0,0,0,0.91); 
  box-shadow: -8px 7px 20px -1px rgba(0,0,0,0.91);
  h2 {
    color: #B19CD9;
    font-weight: 700;
    font-family: merriweather;
    font-size: 25px;
  }
  p, span {
    color: #CDB7F6;
    font-weight: 700;
    font-family: copperplate;
    font-size: 20px;
  }
  div {
    width: 90%;
  }
  :hover {
    cursor: pointer;
  }
  @media screen and (max-width: 700px) {
    height: 180px;
    h1 {
      font-size: 20px;
    }
    p, span {
      font-size: 3vw;
    }
    div {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
`;

export const BoxDivMaster = styled.div`
  width: 100%;
`;

function Roadmap() {
  useEffect(() => {
    AOS.init({
      duration : 2000
    });
  }, []);

  return (
    <Container id="roadmap">
        <h1>Roadmap Phase 1</h1>
        <MasterContainer>
          <LineDiv>
          <div></div>
          <div style={{top: "250px"}}></div>
          <div style={{top: "440px"}}></div>    
          </LineDiv>
          <BoxDivMaster>
            <BoxDiv data-aos="fade-right">
            <div>
              <h2>Pre-sale 1</h2>
              <p>150 quota @0.66SOL</p>
              <span>Pre-sale to celebrities, our OG members and community contributors</span>
            </div>
            </BoxDiv>
            <BoxDiv data-aos="fade-right">
            <div>
              <h2>Pre-sale 2</h2>
              <p>300 quota @0.66SOL</p>
              <span>Pre-sale to whitelisted early supporters</span>
            </div>
            </BoxDiv>
            <BoxDiv data-aos="fade-right">
            <div>
              <h2>Public Mint</h2>
              <p>1399 quota @0.88SOL</p>
              <span>Public mint to all Shiba lovers</span>
            </div>
            </BoxDiv>
            <BoxDiv data-aos="fade-right">
              <div>
                <h2>Listing on Secondary Market</h2>
                <p>Any Unsold NFTs will be listed on Secondary Market.</p>
              </div>
            </BoxDiv>
          </BoxDivMaster> 
        </MasterContainer>
        <h1>Roadmap Phase 2</h1>
        <MasterContainer>
          <LineDiv style={{height: "150vh"}}>
          <div></div>
          <div style={{top: "250px"}}></div>
          <div style={{top: "440px"}}></div>    
          <div style={{top: "640px"}}></div> 
          <div style={{top: "840px"}}></div> 
          </LineDiv>
          <BoxDivMaster>
            <BoxDiv style={{height: "220px"}} data-aos="fade-right">
            <div>
              <h2>Giveaways</h2>
              <p>Grand giveaways will be hosted for our royal NFT holders on a regular basis which aim to reward our community. Prizes may include iPhones, luxuries and SOL etc. Our OG members will have a higher chance of winning.</p>
            </div>
            </BoxDiv>
            <BoxDiv data-aos="fade-right">
            <div>
              <h2>Airdrops</h2>
              <p>Random Shiba NFTs with high rarity will be randomly airdropped to the wallet of our OG members.</p>
            </div>
            </BoxDiv>
            <BoxDiv style={{height: "220px"}} data-aos="fade-right">
            <div>
              <h2>Staking</h2>
              <p>Our Shiba NFT holders can stake Shiba NFTs to earn passive income. The amount of tokens will be determined by the number of NFTs you hold. The more you hold, the more you earn.<br/>
Further details to be announced.</p>
            </div>
            </BoxDiv>
            <BoxDiv style={{height: "220px"}} data-aos="fade-right">
              <div>
                <h2>Party</h2>
                <p>Our Shiba NFT holders can stake Shiba NFTs to earn passive income. The amount of tokens will be determined by the number of NFTs you hold. The more you hold, the more you earn.
Further details to be announced.</p>
              </div>
            </BoxDiv>
            <BoxDiv data-aos="fade-right">
              <div>
                <h2>DAOs</h2>
                <p>Our Shiba NFT holders can access to the DAO and have a vote for the future development of our project. YOU will be deciding our future and metaverse development.</p>
              </div>
            </BoxDiv>
            <BoxDiv data-aos="fade-right">
              <div>
                <h2>Future</h2>
                <p>Dive into the metaverse….</p>
              </div>
            </BoxDiv>
          </BoxDivMaster> 
        </MasterContainer>
    </Container>
  )
}

export default Roadmap