import React, {useState, useRef} from 'react';
import { useOnClickOutside } from './Hooks';
import styled from "styled-components";
import logo from '../assets/logo.png';
import discord from '../assets/discord.png';
import instagram from '../assets/instagram.png';
import { Keypair, PublicKey } from '@solana/web3.js';
import { WalletDialogButton, WalletDisconnectButton } from '@solana/wallet-adapter-material-ui';
import { useWallet } from '@solana/wallet-adapter-react';
import { GiHamburgerMenu } from 'react-icons/gi';
import {Link} from 'react-scroll';
import { RiMenu3Line, RiCloseLine } from 'react-icons/ri';
import './Navbar.css';
import {FaDiscord, FaInstagram} from 'react-icons/fa';

export const NavbarContainer = styled.div`
    width: 100%;
    background-color: #fadadd;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    position: sticky;
    top: 0;
    z-index: 5;
    @media screen and (max-width: 700px) {
        height: 90px;
    }
`;

export const Logo = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    img {
        height: 5rem;
        width: 5rem;
    }
    h1 {
        font-size: 2.8vw;
        font-family: copperplate;
        font-weight: 700;
        color: #a386d9;
    }
    @media screen and (max-width: 1050px) {
        h1 {
            font-size: 6vw;
        }
    }
    @media screen and (max-width: 700px) {
        img {
            width: 3.5rem;
            height: 3.5rem;
        }
    }
`;

export const NavbarInfo = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    h1 {
        font-size: 25px;
        color: #a386d9;
        font-family: copperplate;
        cursor: pointer;
        :hover {
            transition-duration: 0.3s, ease-out;
            border-bottom: 5px solid #32557f;
            padding-bottom: 5px;
        }
        :active {
        opacity: 50%;
    }
    }
    img {
        width: 30px;
        height: 30px;
        cursor: pointer;
        :active {
        opacity: 50%;
        }
    }
    @media screen and (max-width: 1050px) {
        display: none;
    }
`;

export const NavbarMaster = styled.div`
    width: 95%;
    align-items: center;
    display: flex;
    height: 100%;
`;

export const StyledButton = styled(WalletDialogButton)`
    background: linear-gradient(180deg, #7845d9 0%, #7845d9 100%);
    color: white;
    font-size: 2.5vw;
    font-weight: bold;
`;

export const StyledLink = styled(Link)`
    font-size: 25px;
    color: #a386d9;
    font-family: copperplate;
    cursor: pointer;
    font-weight: 600;
    :hover {
        transition-duration: 0.3s, ease-out;
        border-bottom: 5px solid #32557f;
        padding-bottom: 5px;
    }
    :active {
    opacity: 50%;
}
`;

export const HamBurger = styled.div`
    display: none;
    @media screen and (max-width: 1050px) {
        display: flex;
        height: 40px;
        align-items: center;
    }
`;

export const OpenContainer = styled.div`
    background-color: #a386d9;
    width: 40%;
    height: 100vh;
    top: 0;
    right: 0;
    position: absolute;
    z-index: -10;
    @media screen and (max-width: 700px) {
        width: 60%;
    }
`;

export const MobileContainer = styled.div`
    display: none;
    @media screen and (max-width: 1050px) {
        display: flex;
        align-items: center;
        justify-content: center;
    }
`;

export const ButtonsContainer = styled.div`
    width: 100%;
    height: 80%;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    display-content: center;
    div {
        width: 90%;
        height: 2px;
        background-color: black;
        margin-bottom: 30px;
    }
`;

export const StyledLinkMobile = styled(Link)`
    margin-bottom: 50px;
`;

export const SocialMobile = styled.div`
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
        width: 30px;
        height: 30px;
        cursor: pointer;
        :active {
        opacity: 50%;
        }
    }
`;

function Navbar() {
    const node = useRef(); 
    useOnClickOutside(node, () => setToggleMenu(false));
    const [toggleMenu, setToggleMenu] = useState(false);
    const wallet = useWallet();
    return (
    <NavbarContainer>
        <NavbarMaster>
        <Logo>
            <img src={logo} alt="logo"/>
            <h1>Shiba Inu Club</h1>
        </Logo>
        <NavbarInfo>
            <StyledLink to="about" spy={true} smooth={true} offset={-99} duration={500}>About</StyledLink>
            <StyledLink to="roadmap" spy={true} smooth={true} offset={-99} duration={500}>Roadmap</StyledLink>
            <StyledLink to="faq" spy={true} smooth={true} offset={-99} duration={500}>FAQ</StyledLink>
            <a href="https://discord.gg/FjSxv5Fggs">
                <img style={{height: "35px"}} src={discord} alt="discord"/>
            </a>
            <a href="https://www.instagram.com/shiba_inu_nfts/">
                <img src={instagram} alt="instagram"/>
            </a>
            {!wallet.connected ? (
            <StyledButton>Connect</StyledButton>
          ) : (
            <WalletDisconnectButton>Disconnect</WalletDisconnectButton> )}
        </NavbarInfo>
        <MobileContainer ref={node}>
            {toggleMenu
          ? <RiCloseLine cursor="pointer" color="#000" size={32} onClick={() => setToggleMenu(false)} />
          : <RiMenu3Line cursor="pointer" color="#a386d9" size={32} onClick={() => setToggleMenu(true)} />}
            {toggleMenu && (
        <OpenContainer>
                <ButtonsContainer>
                <StyledLink style={{marginBottom: 40, fontSize: 35, color: "#fffbde"}} onClick={() => setToggleMenu(false)} to="about" spy={true} smooth={true} offset={-99} duration={500}>About</StyledLink>
                
                <StyledLink style={{marginBottom: 40, fontSize: 35, color: "#fffbde"}} onClick={() => setToggleMenu(false)} to="roadmap" spy={true} smooth={true} offset={-99} duration={500}>Roadmap</StyledLink>
                
                <StyledLink style={{marginBottom: 40, fontSize: 35, color: "#fffbde"}} onClick={() => setToggleMenu(false)} to="faq" spy={true} smooth={true} offset={-99} duration={500}>FAQ</StyledLink>
                    <a style={{position: "absolute", bottom: 200, right: "80%"}} href="https://discord.gg/FjSxv5Fggs">
                        <FaDiscord style={{height: 30, width: 30, color: "#fff"}}/>
                    </a>
                    <a style={{position: "absolute", bottom: 200, right: 10}}href="https://www.instagram.com/shiba_inu_nfts/">
                        <FaInstagram style={{height: 30, width: 30, color: "#fff"}}/>
                    </a>
                <StyledButton style={{marginTop: 150}}>Connect</StyledButton>
                </ButtonsContainer>
        </OpenContainer>
        )}
        </MobileContainer>
        </NavbarMaster>
    </NavbarContainer>
  )
}

export default Navbar